<template>
    <div>
        <h1>刘胡粤的博客</h1>
        <a href="" @click="go">关于刘胡粤</a>
        <article v-for="(article, index) in articles" :key="index" @click="openArticle(article._id)">
            <h2>{{article.title}}</h2>
            <p>{{article.content}}</p>
        </article>
    </div>
</template>

<script>
import {getArticleList} from './service';
import About from '../about/About.vue';
import Article from '../article/Article.vue';

export default {
    data() {
        return {
            articles: []
        };
    },
    created() {
        getArticleList().then(res => {
            this.articles = res.data;
        });
    },
    methods: {
        go(e) {
            e.preventDefault();
            this.$root.currentRoute = '/about';
            window.history.pushState(
                null,
                About,
                '/about'
            );
        },
        openArticle(id) {
            this.$root.currentRoute = '/article';
            window.history.pushState(
                null,
                Article,
                '/article?id=' + id
            );
        }
    }
}
</script>
