<template>
    <div>
        <button type="button"  @click="goHome">首页</button>
        <h2>{{title}}</h2>
        <p>{{content}}</p>
    </div>
</template>

<script>
import Home from '../home/Home.vue';
import {getArticle} from './service';

export default {
    data() {
        return {
            title: '',
            content: ''
        };
    },
    created() {
        getArticle(location.search.split('=')[1]).then(res => {
            this.title = res.data.title;
            this.content = res.data.content;
        });
    },
    methods: {
        goHome() {
            this.$root.currentRoute = '/';
            window.history.pushState(
                null,
                Home,
                '/'
            );
        }
    }
}
</script>